<template>
  <div class="demo">

		<h2>게시판 리스트
			<a href="javascript:;" @click="getList">Data 불러오기</a>
		</h2>
		
		<ul> 
			<!-- <br> uid {{DataList[0].uid}} is {{DataList[0].user_id}} -->
			<br>
			<li v-for="DataList in DataList" v-bind:key="DataList.uid"> 
				{{DataList.uid}} {{DataList.user_id}} {{DataList.password}}
			</li>
		</ul>
		
		<!-- <span v-for="n in 3" v-bind:key="n"> {{n}} </span> -->

  </div>
</template>

<script>
export default {
	created () {
		this.$axios.get("http://localhost:3000/test")
		.then((data) => {
			this.DataList = data.data
		})
	},
	data () {
		return {
			DataList: []
		}
	},
	
	methods:{
		getList() {
			this.$axios.get("http://localhost:3000/test")
			.then((res)=>{
				console.log(res);
			})
			.then((err)=>{
				console.log(err);
			})
		}
	},
}

</script>
